html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  min-height: 100vh;
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
  position: relative;
}
body::before {
  content: "";
  width: 50%;
  height: 50%;
  position: absolute;
  right: 0;
  background: #eef3f6;
  z-index: -1;
  border-bottom-left-radius: 4.375rem;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 3.125rem;
  padding: 2.1875rem;
  max-width: 70.9375rem;
  margin: auto;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.main {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 64em) {
  .main {
    flex-direction: row;
    margin-top: 7.5rem;
  }
}
.main .img_wrapper {
  margin-left: -1.25rem;
}
@media (min-width: 64em) {
  .main .img_wrapper {
    order: 2;
    width: 50%;
    margin-top: -3.75rem;
  }
}
.main .img_wrapper img {
  width: 132vw;
  max-width: 100%;
}
@media (min-width: 64em) {
  .main .img_wrapper img {
    width: initial;
    max-width: initial;
  }
}
@media (min-width: 64em) {
  .main .article {
    order: 1;
  }
}
.main .article__subtitle .bg-blue {
  background: #242942;
  color: white;
  text-transform: uppercase;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 700;
  padding: 0.125rem 0.375rem 0.1875rem 0.5rem;
  border-radius: 3.125rem;
}
.main .article__subtitle .content {
  font-size: 1.125rem;
  font-weight: 400;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 0.1875rem;
  text-transform: uppercase;
  color: #a0a1ac;
  margin-left: 0.625rem;
}
.main .article__title {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 700;
  font-size: 2.375rem;
  color: #242942;
  text-transform: uppercase;
  margin-top: 0.625rem;
}
@media (min-width: 64em) {
  .main .article__title {
    font-size: 3.4375rem;
  }
}
@media (min-width: 64em) {
  .main .article p {
    padding-right: 12.5rem;
  }
}
.main .article__btns {
  display: flex;
  align-items: center;
  text-align: center;
  margin-top: 1.875rem;
  font-size: 0.75rem;
  color: #a0a1ac;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}
@media (min-width: 64em) {
  .main .article__btns {
    margin-top: 3.75rem;
  }
}
.main .article__btns a {
  color: white;
  background: #ff5c5c;
  padding: 0.625rem 1.5625rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  margin-right: 1.25rem;
  transition: all 200ms ease-in-out;
  letter-spacing: 0;
}
.main .article__btns a:hover, .main .article__btns a:focus {
  background: #242942;
  color: #ff5c5c;
}

.header {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .header {
    margin-top: 3.75rem;
  }
}
.header__logo img {
  display: block;
  width: 1.5625rem;
  height: 1.5625rem;
}
.header__btns {
  border: 0;
  background: transparent;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  position: relative;
}
@media (min-width: 40em) {
  .header__btns {
    display: none;
  }
}
.header__btns--open {
  opacity: 1;
  transition: all 300ms ease-in-out;
}
.header__btns--open.close-menu {
  opacity: 0;
  transform: rotate(180deg);
}
.header__btns--close {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  transition: all 300ms ease-in-out;
}
.header__btns--close.open-menu {
  opacity: 1;
  transform: translate(-50%, -50%) rotate(180deg);
}
.header__btns:hover {
  box-shadow: 0 0.375rem 0.75rem -0.1875rem rgba(160, 161, 172, 0.5);
}
.header__nav {
  display: flex;
  visibility: hidden;
  position: absolute;
  top: 5.625rem;
  left: 50%;
  transform: translateX(-50%);
  background: #fff;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.25rem;
  font-weight: 700;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 0.125rem;
  padding: 1.5625rem;
  width: 85%;
  opacity: 0;
  box-shadow: 0 0.625rem 0.9375rem 0.3125rem rgba(160, 161, 172, 0.5);
  transition: all 300ms ease-in-out;
}
@media (min-width: 40em) {
  .header__nav {
    opacity: 1;
    visibility: visible;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transform: initial;
    left: initial;
    position: initial;
    width: initial;
    padding: initial;
    background: initial;
    box-shadow: initial;
    font-size: 1.125rem;
  }
}
.header__nav--active {
  visibility: visible;
  opacity: 1;
}
.header__nav a:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .header__nav a:not(:last-child) {
    margin: 0;
    margin-right: 1.25rem;
  }
}
.header__nav hr {
  display: block;
  height: 0.0625rem;
  width: 100%;
  opacity: 0.3;
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .header__nav hr {
    border: none;
    border-left: 1px solid #73848c;
    height: 50%;
    width: 1.5625rem;
    margin: 0;
  }
}
.header__link {
  color: #242942;
  transition: all 300ms ease-in-out;
}
.header__link--gray {
  color: rgba(91, 95, 113, 0.5);
  pointer-events: none;
}
.header__link:hover, .header__link:focus {
  text-shadow: 0.1875rem 0.3125rem 0.1875rem rgba(91, 95, 113, 0.5);
  outline: none;
}